html {
  font-family: 'Share Tech Mono', monospace;
  font-size: 15px;
  line-height: 1.1;
  overflow: hidden;
}

body {
  margin: 0;
  overflow: hidden;
}

canvas {
  width: 100vw;
  height: 100vh;
}

p {
  margin: 0;
}

.meta {
  position: absolute;
  top: 1.2em;
  left: 1em;
  right: 1em;
}

.meta__fade {
  opacity: 0.3;
}
